* {
    box-sizing: border-box;
    margin: 0;
}

:root {
    font-size: 1.5rem;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-family: 'Questrial-Regular';
    /* colors */
    --cool-cream: #eeeeee;
    --cool-pink: #acc6aa;
    --cool-teal: #71a0a5;
    --cool-blue: #77628c;
    --cool-watermelon: #d33958;
}

html {
  height: 100%;
}

body {
  height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: var(--cool-cream);
}

.headr {
    background-color: darkslategray;
    background-color: white;
    color:white;
    top: 0;
    position: sticky;
}

.nav-bar {
  background-color: var(--cool-pink);
  box-shadow: 5px 2px 2px lightgray;
  border-bottom-left-radius: 70%;
  border-bottom-right-radius: 70%;
  padding: 1rem;
}

.nav-ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
}

.nav-ul a {
  text-decoration: none;
  color: var(--cool-cream);
  font-weight: bold;
}

.nav-ul a:hover,
.nav-ul a:active {
  color: white;
}


.game-area-container {
    width: 100%;
}

  
.modal-content {
  background-color: #fefefe;
  margin: 15% auto; 
  padding: 20px;
  border: 1px solid #888;
  width: 80%; 
}

#portal .modal-container {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100vh;
  display: flex;
  background: rgba(0, 0, 0, 0.7);
}

.modal-container .modal {
  z-index: 1;
  width: 42%;
  height: 20%;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  animation: animate 0.3s;
  
  flex-wrap: wrap;
  overflow: scroll;
  position: absolute;
  right: 0;
  bottom: 0;
}


.modal > h4 {
  margin-left: 5%;
  color: var(--cool-blue);
  margin-block: 3%;
}

.modal button {
  position: absolute;
  top: 7px;
  right: 7px;
  padding: 8px 11px;
  background: crimson;
  color: white;
  font-weight: bold;
  border: none;
  outline: none;
  border-radius: 2px;
  cursor: pointer;
}

.thumbnails-div {
  width: 100%;
}

.person-preview {
  display: flex;
  justify-content: center;
  background-color: white;
}

.person-preview:hover,
.person-preview:active {
  background-color: var(--cool-cream);
}

.thumbnail {
  max-width: 10%;
}

.finder-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 10%;
}

.finder-li {
  width: 20%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.finder-thumbnail {
  min-width: 22%;
}

.finder-thumbnail:last-of-type {
  max-width: 20%;
}

.grayed {
  filter: brightness(2.25) grayscale(1);
}

.home-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.home-container  {
  font-size: 1.5rem;
  color: var(--cool-blue);
}

.home-container,
.level-previews {
  margin-top: 1%;
}

.level-select {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5%;
  color: var(--cool-teal);
  font-size: 1rem;
}

.level-previews {
  display: flex;
  justify-content: center;
  gap: 5%;
}

.level-thumbnail {
  width: 26%;
  height: 44%;
}

.level-thumbnail > img {
  max-width: 100%;
  max-height: 10%;
}

.level-thumbnail > button {
  width: 100%;
  height: 1.5rem;
  min-height: max-content;
  background-color: var(--cool-cream);
  border: solid var(--cool-blue) 1px;
  color: var(--cool-teal);
  font-weight: bold;
  font-size: 60%;
}

.level-thumbnail > button:hover,
.level-thumbnail > button:active {
  background-color: var(--cool-pink);
  color: var(--cool-cream);
  cursor: pointer;
  border: transparent;
}

.level-thumbnail > img:hover,
.level-thumbnail > img:active {
  animation: fadein 0.9s ease-in-out alternate infinite;
  cursor: pointer;
}


.person-name-span {
  color: var(--cool-blue);
}

.person-name-span::first-letter {
  text-transform: capitalize;
}

.victory-modal-container {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.7);
}

.victory-modal-container .victory-modal {
  z-index: 1;
  width: 42%;
  height: 20%;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  animation: animate 0.3s;
  
  flex-wrap: wrap;
  overflow: scroll;
}
.victory-modal-container .victory-modal {
  flex-direction: column;
  justify-content: space-around;
  border: solid var(--cool-pink) 5px;
  overflow: hidden;
}
  
.victory-modal-container .victory-modal {
text-align: center;
  height: max-content;
  background-color: var(--cool-cream);
  padding: 0;
}

.victory-modal > * {
  margin: 1%;
}


.victory-modal > h5 {
  font-size: 2rem;
  color: var(--cool-watermelon);
}

.victory-pupper {
  width: 30%;
}

.victory-pupper:active {
  transform: scale(1.2);
}

/* victory modal button */
.victory-modal > button {
  width: 70%;
  height: 1.5rem;
  min-height: max-content;
  background-color: var(--cool-cream);
  background-color: var(--cool-pink);
  border: solid var(--cool-blue) 1px;
  color: var(--cool-teal);
  color: var(--cool-cream);
  font-weight: bold;
  font-size: 120%;
}

.victory-modal > button:hover,
.victory-modal > button:active {
  cursor: pointer;
  background-color: var(--cool-watermelon);
  border: transparent;
  color: var(--cool-cream);
}

.victory-modal > p {
  font-size: 100%;
  font-style: italic;
  color: var(--cool-teal);
}

.victory-input {
  width: 70%;
  font-size: 1.1rem;
  text-align: center;
  letter-spacing: 1px;
  color: crimson;
}

.victory-modal > .stopwatch-span {
  color: var(--cool-blue);
}

/* scores page */

  .scores-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
  }

  .scores-table {
    margin-bottom: 5%;
  }

  .scores-table th {
    background-color: var(--cool-pink);
    width: 20%;
  }

  .scores-table td {
    padding: 1%;
    border-bottom: solid 1px var(--cool-teal);
  }

  .th-name,
  .td-name {
    border-left: solid 1px var(--cool-pink);
    border-right: solid 1px var(--cool-pink);
  }

  .hall-fame {
    color: var(--cool-watermelon);
    color: darkgray;
    font-size: 300%;
    margin-block: 2.2%;
  }

  .hall-fame-foot {
    color: gray;
  }

  @keyframes animate {
    from {
      transform: scale(0.5);
    }
    to {
      transform: scale(1);
    }
  }

  @keyframes fadein {
    from {opacity: 1;}
    to {opacity: 0.69;}
  }

  @font-face {
    font-family: Questrial-Regular;
    src: url('./fonts/Questrial-Regular.ttf') format('truetype');
  }

  /* mobile */
  
  @media only screen and (max-width: 480px) {

    .headr {
      padding-bottom: 0;
      position: sticky;
      top: 0;
    }

    .home-container,
    .level-previews {
      margin-top: 10%;
    }

    .level-thumbnail {
      width: 39%;
      height: 66%;
    }

    .level-thumbnail > img {
      width: 100%;
      height: 100%;
    }


  .level-previews {
    gap: 8%;
  }

    .modal-container .modal {
      z-index: 1;
      width: 52%;
      height: 20%;
      background: white;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      border-radius: 5px;
      animation: animate 0.3s;
      
      flex-wrap: wrap;
      overflow: scroll;
    }

    .modal > h4 {
      margin-left: 5%;
      margin-top: 7%;
      color: var(--cool-blue);
    }



  .thumbnails-div {
    width: 100%;
  }
  
  .person-preview {
    display: flex;
    justify-content: space-between;
    align-items: center; 
  }

  .person-preview > span {
    width: 100%;
  }


  .thumbnail {
    max-width: 36%;
  }


  .finder-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-left: 7%;
  }

  .finder-li {
    width: 100%;
    list-style-type: none;
  }

  .finder-thumbnail {
    min-width: 49%;
    max-width: 60%;
  }

  .finder-thumbnail:last-of-type {
    max-width: 42%;
  }


  .victory-pupper {
    width: 42%;
  }

  .victory-modal > p {
    font-size: 70%;
  }

  .victory-input {
    width: 80%;
    font-size: 0.8rem;
    text-align: center;
    letter-spacing: 1px;
  }

  .victory-modal > button {
    width: 80%;
    height: 1.5rem;
    min-height: max-content;
    background-color: var(--cool-cream);
    border: solid var(--cool-blue) 1px;
    color: var(--cool-teal);
    font-weight: bold;
    font-size: 60%;
  }

  .victory-modal > .stopwatch-span {
    font-size: 90%;
    
  }

  .hall-fame {
    margin-block: 9%;
  }

  .hall-fame-foot {
    margin-top: 9%;
  }

  }